@import url(https://fonts.googleapis.com/css2?family=Limelight&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
/* Polices */

/*
1. RESET
2. Blocks
3. Inline
4. ID
5. Classes
*/

:root {
  --body: #0f0f0f;
  --rouge: #ed4c4c;
  --rougeop: rgba(237, 76, 76, 0.2);
  --rougealt: #fe3c3c;
  --marron: #a71f17;
  --blanc: #fffef4;
  --lumiere: linear-gradient(rgba(156, 217, 244, 3), rgba(42, 61, 69, 3));
  --metal: linear-gradient(0deg, rgba(0, 0, 0, 3), rgba(33, 33, 33, 3));
  --popcorn: #ffd092;
  --orange: #ffb049;
}

/* 1 RESET */

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote::before,
blockquote::after,
q::before,
q::after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

/* 2 Block */

html {
  background-color: #0f0f0f;
  background-color: var(--body);
}

body {
  background-color: #0f0f0f;
  background-color: var(--body);
